<template>
  <div class="fileBox">
    <!-- 文件按键 -->
    <div class="upload_file">
      上传文件
      <input
        class="ipt"
        type="file"
        id="people-file"
        multiple="multiple"
        @change="changefile"
      />
    </div>
    <!-- 文件夹按键 -->
    <div class="upload_folder">
      上传文件夹
      <input
        class="ipt"
        type="file"
        id="people-folder"
        webkitdirectory=""
        @change="changefile"
      />
    </div>
    <!-- 上传列表 -->
    <div class="filesList">
      <!-- 总进度 -->
      <totSpeed
        :totalSpeed="totalSpeed"
        :filesListLength="filesList.length"
        :tot="tot"
      >
      </totSpeed>
      <fileList :filesList="filesList"> </fileList>
    </div>
  </div>
</template>
<script>
import totSpeed  from "./totSpeed.vue";
import fileList from "./fileList.vue";
export default {
  data() {
    return {
      filesList: [], //上传文件数组
      tot: 0,        //总进度
    };
  },
  components: {
    totSpeed,
    fileList,
  },

  computed: {
    // 计算总进度
    totalSpeed() {
      let tot = 0;                        //总进度
      let len = this.filesList.length;    //文件个数
      if (len) {
        tot =
          this.filesList.reduce((pre, cur) => {
            return (pre += cur.speed);
          }, tot) / len;
      }
      return tot;
    },
  },
  methods: {
    // 点击上传获取文件信息
    changefile(e) {
      this.filesList = [];     //清空已上传列表
      let files = e.target.files;
      //保存文件上传的信息
      for (let key of files) {
        let Obj = {
          name: key.name,
          size: key.size,
          date: key.lastModifiedDate,
          speed: 0,
        };
        this.filesList.push(Obj);
        this.uploading(Obj, Obj.speed); //进度条显示
      }
    },
    // 模拟进度条增加
    uploading(obj, speed) {
      let interval = Math.floor(Math.random() * 10) + 1; //进度每次增长间隔
      let time = setInterval(() => {
        let num = Math.random();         //每次进度量
        if (speed <= 100) {
          this.$set(obj, "speed", (speed += num));
        } else {                          //进度超出100 就设置100
          this.$set(obj, "speed", 100);
          this.tot++;                      //100时就计入完成数量
          clearInterval(time);
        }
      }, interval * 10);
    },
  },
};
</script>
<style lang="less" scoped>
.upload_file,
.upload_folder {
  margin: 50px 10px;
  position: relative;
  display: inline-block;
  background: #41a3ff;
  border-radius: 4px;
  padding: 4px 12px;
  overflow: hidden;
  color: #ffffff;
  text-decoration: none;
  text-indent: 0;
  line-height: 20px;
  transition: 0.3s;
  input {
    cursor: pointer;
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
  }
  &:hover {
    background: #003ce2;
    color: #ffffff;
    text-decoration: none;
  }
}
.filesList {
  width: 90%;
  height: 700px;
  margin: 0 auto;
  padding: 5px;
  box-sizing: border-box;
  overflow: hidden;
  overflow-y: auto;
}
</style>
