<template>
    <div class="totBox" v-show="totalSpeed && filesListLength > 1">
            <!-- 总进度百分比 -->
        <div class="totspeedNum">
            <p  v-if="totalSpeed < 100 ">{{tot}} / {{filesListLength}}  总进度 {{totalSpeed.toFixed(2)}} %</p>
            <transition name="list_group">
                <p v-if="totalSpeed == 100">全部上传完成 !</p>
            </transition>
        </div>
        <!-- 总进度条 -->
        <progress class="totalSpeed" :value="totalSpeed" max="100" ></progress>
    </div>
</template>
<script>
export default {
    props:['totalSpeed', 'filesListLength' , 'tot']
}
</script>
<style lang="less" scoped>
    .totBox{
        position: absolute;
        top: 110px;
        left: 50%;
        transform: translateX(-50%);
        width: 90%;

        .totspeedNum{
            p{
                line-height: 20px;
                color: #fff;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
            }
        }
        .totalSpeed{
            width: 90%;
            height: 20px;
            background: #fff;
            &::-webkit-progress-bar{
                background-color: #a5a5a5;
                border-radius: 10px;
            }
            /* 进度条完成长度背景 */
            &::-webkit-progress-value{
                background: #003ce2;
                border-radius: 10px;
            }
        }
    }
</style>