<template>
    <!-- 上传文件列表 -->
    <transition-group name="list_group" >
        <div class="filesListItem" v-for="item in filesList" :key="item.name">
            <div class="fileBox">
                <!-- 文件图标 -->
                <div class="fileIcon">
                    <span class="iconfont icon--wenjian"></span>
                </div>
                <!-- 文件信息 -->
                <div class="fileMsg">
                    <p>文件名: {{item.name}} </p>
                    <p>大小: {{item.size | fileSize}}</p>
                    <p>时间: {{item.date | time}}</p>
                </div>
                <!-- 成功图标 -->
                <transition name="list_group">
                    <div v-if="item.speed == 100" class="successIcon">
                        <span class="iconfont icon-duigou"></span>
                    </div>
                </transition>
                <!-- 显示百分比进度 -->
                <transition name="list_group">
                    <div v-if="item.speed !== 100" class="successIcon">
                        <span class="speedNum">{{item.speed.toFixed(2)}} %</span>
                    </div>
                </transition>
            </div>
            <!-- 进度条 -->
            <progress class="speed" :value="item.speed" max="100" ></progress>
        </div>
    </transition-group>
</template>
<script>
export default {
    props:['filesList'],
    filters:{
        // 时间格式化
        time(date){
            let y = date.getFullYear()
            let m = date.getMonth() + 1
            m = m < 10 ? ('0' + m) : m
            let d = date.getDate()
            d = d < 10 ? ('0' + d) : d
            let h =date.getHours()
            h = h < 10 ? ('0' + h) : h
            let M =date.getMinutes()
            M = M < 10 ? ('0' + M) : M
            let s =date.getSeconds()
            s = s < 10 ? ('0' + s) : s
            return y + '/' + m + '/' + d + ' ' + h + ':' + M + ':' + s;
        } ,
        // 大小格式化
        fileSize(size){
            let s
            if( size < 0.1 * 1024 ){ //如果小于0.1KB转化成B  
                s = size.toFixed(2) + "B";    
            }else if(size < 0.1 * 1024 * 1024 ){//如果小于0.1MB转化成KB  
                s = (size / 1024).toFixed(2) + "KB";              
            }else if(size < 0.1 * 1024 * 1024 * 1024){ //如果小于0.1GB转化成MB  
                s = (size / (1024 * 1024)).toFixed(2) + "MB";  
            }else{ //其他转化成GB  
                s = (size / (1024 * 1024 * 1024)).toFixed(2) + "GB";  
            }  
            return s
        }
    },
}
</script>
<style lang="less" scoped>
/* 过度动画 */
.list_group-enter-active,
.list_group-leave-active {
    transition: all .5s;
}
.list_group-enter,
.list_group-leave-to {
    opacity: 0;
    transform: translateX(50px);
}
.filesListItem{
        margin: 5px auto;
        width: 90%;
        height: 50px;
        text-align: left;
        font-size: 14px;
        line-height: 16px;
        border: 1px solid #ddd;
        background: rgb(243, 243, 243);
        border-radius: 5px;
        padding: 10px;
        .fileBox{
            overflow: hidden;
            .fileIcon{
                float: left;
                .iconfont{
                    font-size: 40px;
                    line-height: 50px;
                    color: rgb(0, 128, 248);
                }
            }
            .fileMsg{
                float: left;
                margin-left: 20px;
            }
            .successIcon{
                float: right;
                .iconfont{
                    font-size: 20px;
                    line-height: 50px;
                    color: rgb(0, 57, 214);
                }
                .speedNum{
                    font-size: 16px;
                    line-height: 50px;
                }
            }
        }
        .speed{
            width: 100%;
            height: 5px;
            display: block;
            background: #fff;
            
            /* 进度条总长度背景 */
            &::-webkit-progress-bar{
                background-color: #d1d1d1;
                border-radius: 10px;
            }
            /* 进度条完成长度背景 */
            &::-webkit-progress-value{
                background: #0084ff;
                border-radius: 10px;
            }
        }
        
    }
</style>